.newMovies {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  width: 100%;
}

.newMovies a {
  width: 100%;
  display: block;
  margin-bottom: 30px;
}

@media(min-width: 576px) {
  .newMovies {
    -ms-flex-pack: justify;
        justify-content: space-between;
  }
  .newMovies a {
    width: 48%;
  }
}

@media(min-width: 768px) {
  .newMovies a {
    width: 31.5%;
  }
}

@media(min-width: 992px) {
  .newMovies a {
    width: 23%;
  }
}

.movieLink {
  position: relative;
}

.movieInfo {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  position: absolute;
  top: 0;
  text-align: center;
  padding: 0 10px;
  width: 100%;
  height: 100%;
  background-color: #263238;
  color: #1DE9B6;
  opacity: 0;
  visibility: hidden;
}

.movieLink:hover .movieInfo {
  opacity: 1;
  visibility: visible;
}



